import Vue from 'vue'
import VueResource from 'vue-resource'
import { API_ROOT } from 'src/config'
import Cache from 'libs/cache'

Vue.use(VueResource)

let memory = new Map()

class Api {

  constructor (memory)
  {
    this.memory = memory
  }

  /**
   * [Api]
   * 
   *
   * @param Object params 
   * @param function success 
   * @param function errors  
   *
   * @return function success 
   * @return function errors  
   */
  request (params = {}, success, errors)
  {
    // 
    Vue.http.options.headers = {
    }
    Vue
      .http({
        // 
        root: API_ROOT,
        // api
        url: params.url || '',
        // 
        method: params.method || GET,
        // 
        params: params.data === Object(params.data) ? params.data : {},
        // 
        timeout: 5000,
      })
      .then((response) => {     // 
        success(response.data)
      }
      ,(response) => {      // 
        errors(response)
      })
  }

  /**
   * 
   */
  search (query = {}, success, errors)
  {
    let _request = {
      url: 'goods/search',
      method: 'GET',
      data: query, // {cid, keywords, ids, tag, page, per_page...}
    }

    let key = JSON.stringify(_request)
    if (this.memory.has(key)) {
      setTimeout(() => success(this.memory.get(key)), 0)
    } else {
      this.request (
        _request,
        (res) => {
          setTimeout(() => success(res), 0)
          this.memory.set(key, res)
          console.log('===> ')
        },
        (res) => {
          setTimeout(() => errors(res), 0)
          console.log('===> !', res)
        }
      )
    }
  }

  /**
   * 
   */
  getGoodsCategoryTree (success, errors)
  {
    let _request = {
      url: 'goods/category/tree',
      method: 'GET',
      data: {}
    }
    let key = 'goods_category_tree'
    let value = Cache.get (key)
    if (null !== value) {
      setTimeout(() => success(value), 0)
    } else {
      this.request (
        _request,
        (res) => {
          setTimeout(() => success(res), 0)
          setTimeout(() => Cache.set (key, res, { exp: 24*60*60 }), 0)
          console.log ('===> ')
        },
        (res) => {
          setTimeout(() => errors (res), 0)
          console.log ('===> !', res)
        }
      )
    }
  }


}

export default new Api(memory)



