<template>
<div class='loading'>
  <slot></slot>
  <p></p>
  <mt-spinner :type="2" color="rgb(100, 100, 100)" :size="30"></mt-spinner>
</div>
</template>

<style scoped>
  .loading {
      color: #777;
      text-align: center;;
      font-size: 0.8rem;
      margin-top: 20px;
      margin-bottom: 50px;
  }
</style>

<script>
  // mint-ui 
  //  babel-plugin-component 
  // : http://mint-ui.github.io/docs/#!/zh-cn
  import Vue from 'vue'
  import { Spinner } from 'mint-ui';
  Vue.use( Spinner )

  export default {
    components: {
      mtSpinner: Spinner
    }
  }
</script>



